@font-face {
    font-family: 'ProximaNova';
    src:
        url('./assets/fonts/ProximaNova-Bold.woff2') format('woff'), /* Modern Browsers */
        url('./assets/fonts/ProximaNova-Bold.ttf') format('truetype'), /* Safari, Android, iOS*/
        url('./assets/fonts/ProximaNova-Bold.svg') format('svg'); /* Legacy iOS*/
    font-weight: 700;
    font-style: normal;
    font-display: swap;
}

