@media screen and (max-width: 991px) {
    .container {
        padding-left: 20px;
        padding-right: 20px;
    }
    .navbar {
        padding-top: 16px;
        padding-bottom: 18px;
        &-brand {

            img{
                max-width: 126px;
            }
        }
        
            &-collapse {
                display: flex;
                flex-direction: column;
                background-color: $bg-secondary;
                margin: 0 -20px;
                padding: 0 40px;
                transform: translate(0, 16px);
              }

              &-link {
                padding: 30px;
              }

              &-nav {
                padding: 37px 0 23px;

                a {
                    padding: 0 0 11px;
                    font-size: 24px;
                    text-align: 222%;
                }
              }

              .btn {
                margin: 0 auto 60px;
                padding: 22px 26px;
                width: 100%;
                max-width: 295px;
                justify-content: center;
              }
    }
     
    .first-screen {
      .container {
        padding: 0 28px;
        text-align: center;
        display: flex;
        flex-direction: column;
        align-items: center;
        z-index: 1;
      }

      .container-xxl {
        max-height: calc(100vh - 74px);
        position: relative;
        &::after {
          content: "";
          position: absolute;
          background-color: rgba(0, 0, 0, 0.445);
          left: 0;
          right: 0;
          top: 0;
          bottom: 0;
        }
      }

      &-slider__title {
        font-size: 32px;
        max-width: 295px;
        margin-bottom: 10px;
      }

      &-slider__subtitle {
        font-size: 16px;
        margin-bottom: 18px;
      }
    }

    .calculation__img-box {
      display: none;
    }
}



